import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, Platform } from 'ionic-angular';

/**
 * Generated class for the ApplyCashlineWaitingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-apply-cashline-waiting',
  templateUrl: 'apply-cashline-waiting.html',
})
export class ApplyCashlineWaitingPage {
  unregisterBackButtonAction;

  min: any;
  sec: any;
  timeSecond = 72;
  retryCount;
  constructor(
    public navCtrl: NavController, 
    public platform: Platform,
    public app: App,
    public navParams: NavParams) {
  }

  ionViewWillLeave() {
    // Unregister the custom back button action for this page
    this.unregisterBackButtonAction && this.unregisterBackButtonAction();
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad ApplyCashlineWaitingPage');
    this.timer();
    this.retryCount = (this.timeSecond / 10) - 2;
  }

  timer() {
    this.displayTime(this.timeSecond)

    const countDown = setInterval(() => {
      this.timeSecond--;
      this.displayTime(this.timeSecond)

      if (this.timeSecond < 1) {
        clearInterval(countDown);
      }
    }, 1000)

  }

  displayTime(second) {
    const min = Math.floor(second / 60);
    const sec = Math.floor(second % 60);

    this.min = min < 10 ? `0${min}` : min;
    this.sec = sec < 10 ? `0${sec}` : sec;;
  }

}



